/* Theme Switch */
.toggleContainer {
  font-size: 15px;
  display: flex;
  align-items: center;
  gap: 12px;
  width: fit-content;
}

.toggleWrapper {
  position: relative;
  width: 4em;
  height: 2.2em;
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
}

.toggleText {
  font-size: 0.95rem;
  color: #000;
  word-break: break-word;
}

.darkMode .toggleText {
  color: #f1f1f1ea;
}

.darkModeInput {
  display: none;
}

.toggleButton {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  background: transparent;
  border: 1px solid #000;
  border-radius: 8px;
  padding: 8px 12px;
  cursor: pointer;
  font: inherit;
  transition: all 0.2s;
  flex-wrap: wrap;
}

.darkMode .toggleButton {
  border-color: #fff;
}

.toggleButton:focus {
  outline-color: rgb(0, 0, 0);
  outline-offset: 3px;
}

/* The slider - visuel inversé: bleu par défaut */
.toggleLabel {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #00a6ff;
  transition: 0.4s;
  border-radius: 30px;
  border: 1.5px solid #2a2a2a;
  overflow: hidden;
  pointer-events: none;
}
.darkMode .toggleLabel {
  border: 1px solid #fff;
}

.toggleLabel:focus {
  outline: none;
}

/* Thumb - soleil par défaut à droite */
.toggleThumb {
  position: absolute;
  content: "";
  height: 1.2em;
  width: 1.2em;
  border-radius: 20px;
  right: 0.5em;
  bottom: 0.5em;
  transition: 0.4s;
  transition-timing-function: cubic-bezier(0.81, -0.04, 0.38, 1.5);
  box-shadow: inset 15px -4px 0px 15px #ffcf48;
}

/* Mode dark: fond sombre */
.inputChecked + .toggleLabel {
  background-color: #2a2a2a;
}

/* Mode dark: thumb devient lune et va à gauche */
.inputChecked + .toggleLabel .toggleThumb {
  transform: translateX(calc(-1.8em + 4px));
  box-shadow: inset 8px -4px 0px 0px #fff;
  bottom: calc(0.5em - 2px);
}

/* Étoiles (visibles en mode dark avec la lune) */
.leftIcon {
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  width: 5px;
  height: 5px;
  transition: all 0.4s;
  opacity: 0;
}

.star1 {
  left: 2.5em;
  top: 0.5em;
}

.star2 {
  left: 2.2em;
  top: 1.2em;
}

.star3 {
  left: 3em;
  top: 0.9em;
}

/* Nuage (visible en mode clair avec le soleil) */
.rightIcon {
  width: 3.5em;
  position: absolute;
  bottom: -1.4em;
  left: -1.1em;
  opacity: 1;
  transition: all 0.4s;
}

/* Mode dark: étoiles visibles */
.inputChecked + .toggleLabel .leftIcon {
  opacity: 1;
}

/* Mode dark: nuage caché */
.inputChecked + .toggleLabel .rightIcon {
  opacity: 0;
}


/* From Uiverse.io by njesenberger */ 
.toggleContainer {
  --knob-size: 1.75em;
  display: flex;
  justify-content: center;
  position: relative;
}

.toggleInput {
  position: absolute;
  z-index: 2;
  bottom: 132.5%;
  border-radius: 50%;
  transform: rotate(-25deg);
  transform-origin: 50% 4.75em;
  width: var(--knob-size);
  height: var(--knob-size);
  opacity: 0;
  /* fix em sizing */
  font: inherit;
  transition: transform .24s cubic-bezier(.65, 1.35, .5, 1);
  cursor: pointer;
}

.toggleInput:checked {
  transform: rotate(25deg);
}

.toggleHandleWrapper {
  position: absolute;
  z-index: 1;
  bottom: -135%;
  -webkit-mask-image: linear-gradient(to bottom, #000 62.125%, transparent 50%);
  mask-image: linear-gradient(to bottom, #000 62.125%, transparent 50%);
  width: 200%;
  overflow: hidden;
}

.toggleHandle {
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: rotate(-25deg);
  transform-origin: bottom center;
  transition: transform .24s cubic-bezier(.65, 1.35, .5, 1);
}

.toggleInput:checked + .toggleHandleWrapper > .toggleHandle {
  transform: rotate(25deg);
}

.toggleHandleKnob {
  position: relative;
  z-index: 1;
  border-radius: 50%;
  width: var(--knob-size);
  height: var(--knob-size);
  background-image: radial-gradient(farthest-corner at 70% 30%, #fedee2 4%, #d63534 12% 24%, #a81a1a 50% 65%, #d63534 75%);
  transition: transform .24s cubic-bezier(.65, 1.35, .5, 1);
}

.toggleInput:checked + .toggleHandleWrapper .toggleHandleKnob {
  transform: rotate(-90deg);
}

/* toggle handle knob hover inner shadow */
.toggleHandleKnob::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  box-shadow: inset 0 0 8px 2px rgb(255 255 255 / .4);
  opacity: 0;
  transition: opacity .2s;
}

@media (hover: hover) {
  .toggleInput:hover + .toggleHandleWrapper .toggleHandleKnob::after,
  .toggleInput:focus-visible + .toggleHandleWrapper .toggleHandleKnob::after {
    opacity: 1;
  }
}

.toggleHandleBarWrapper {
  position: relative;
  width: .5em;
  height: 3em;
}

.toggleHandleBar {
  position: absolute;
  top: calc(var(--knob-size) / 2 * -1);
  left: 0;
  width: 100%;
  height: calc(100% + var(--knob-size) / 2);
  background-image: linear-gradient(to right, #777475, #a4a4a4, #fff 45% 55%, #a4a4a4, #777475);
  background-position-x: .06125em;
  transition: background-position-x .24s cubic-bezier(.65, 1.35, .5, 1);
  box-shadow: inset 0 1em .25em rgb(0 0 0 / .4);
}

.toggleInput:checked + .toggleHandleWrapper .toggleHandleBar {
  background-position-x: -.06125em;
}

.toggleBase {
  position: relative;
  border-radius: 3.125em;
  padding: .25em;
  width: 3.5em;
  height: 1.125em;
  background-color: #fff;
  background-image: linear-gradient(to bottom, #fff, #d7d7d7);
  box-shadow: 0 -.25em .5em #fff, 0 .25em .5em #d7d7d7;
}

.toggleBaseInside {
  position: relative;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, #a6a6a6, #7d7d7d);
  box-shadow: inset 0 .0625em rgb(255 255 255 / .2), inset 0 -.03125em rgb(255 255 255 / 1), inset 0 -.0625em .25em rgb(0 0 0 / .1);
}

/* toggle base inside active */
.toggleBaseInside::after {
  content: '';
  position: absolute;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, #5ab054, #438c3c);
  box-shadow: inherit;
  opacity: 0;
  transition: opacity .24s cubic-bezier(.65, 1.35, .5, 1);
}

.toggleInput:checked ~ .toggleBase .toggleBaseInside::after {
  opacity: 1;
}